import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { GalleryServiceProvider } from '../../providers/gallery/gallery';
import * as app_globals from '../../app/app-globles';
import { CategoryPage } from '../category/category';


/**
 * Generated class for the GalleryPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-gallery',
  templateUrl: 'gallery.html',
})
export class GalleryPage {
  public floors: any = [];
  public selectedFloor:String="";
  public items: any = [];
  public contentURL: any;
  constructor(public navCtrl: NavController, public navParams: NavParams, public galleryServiceProvider: GalleryServiceProvider) {
    this.contentURL = app_globals.k_content_url;
    galleryServiceProvider.getFloors().subscribe(
      successResponse => {
        this.floors = successResponse;
        this.selectFloor(this.floors.floors[0]);
      });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad GalleryPage');
  }
  selectFloor(params) {
    if (params) {
      this.selectedFloor= 'කාණ්ඩ ('+params.name_si+') பிரிவுகள் ('+params.name_ta+') Categories ('+params.name_en+')';
     
      this.galleryServiceProvider.getItemByFloor(params.id, 'si').subscribe(
        successResponse => {
          this.items = successResponse;
          console.log(successResponse);

        });
    }
  }
  selectCategory(params) {
    if (!params) params = {};
    this.navCtrl.push(CategoryPage, {
      cat: params
    });
  }
}



